body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size:14px;
  color:#ccc;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/*修复img默认位置和IE边框*/
img{
  vertical-align: top;
  border:none;
}

/*去除虚线*/
input:focus,select:focus,textarea:focus,button:focus{
  outline: none;
}
/*重置部分表单圆角和内阴影*/
input[type="text"],input[type="file"],input[type="password"],input[type="search"],input[type="tel"],input[type="number"],input[type="email"],input[type="url"],textarea{
  -webkit-appearance:none;
}
/*去除文本框上下箭头*/
input[type="number"]::-webkit-inner-spin-button{
  -webkit-appearance:none;
}
input{
  padding-left:5px;
}
a{
  text-decoration: none;
  /*ios禁止页面在新窗口打开*/
  -webkit-touch-callout:none;
}
body{
  background-color: #f5f5f5;
}
em,i{
  font-style: normal;
}
li{
  list-style: none;
}
html,body{
  height: 100%;
  background:#fff;
  overflow:hidden;
  /*background:#f1f1f1;*/
}
#root{
  width:100%;
  height: 100%;
  overflow:hidden;
  background:url(../../static/media/timg.5b630ad1.jpg) no-repeat;
  background-size:cover;
}


.home-content{
  position:relative;
  width:1090px;
  margin:0 auto;
  height: 100%;
  overflow:hidden;
}
.container{
  height:100%;
  margin-left:180px;
}
.rig-container{
  width: 100%;
  height: 100%;
  overflow-y:auto;
}
.item-content{
  width: 100%;
  overflow: hidden;
  padding-left:20px;
}

/*statu-item*/

.main-content{
    background:url(../../static/media/chun.e2ac839e.jpg) no-repeat;
    background-size:cover;
    background-attachment:fixed;
    color:#fff;
    padding-top: 20px;
}
.home-staus{
    float: left;
    width:50%;
    padding:0 30px;
    overflow: hidden;
    border-right: 1px solid #ededed;
}
.home-news{
    float: left;
    width:50%;
    padding:0 30px;
    overflow: hidden;
}
.news-item{
    width: 100%;
    margin-bottom:10px;
    border-bottom:1px solid #ededed;
}
.news-item>div{
    width: 100%;
    overflow: hidden;
}
.news-item img{
    width:100%;
    height:auto;
}
.news-item .tit{
    line-height:28px;
    font-size:16px;
    font-weight:bold;
    padding:10px;
}
.card-list{
    width:100%;
    overflow:hidden;
}
.card-list .statu-item{
    width:100%;
    overflow:hidden;
    padding:20px;
    color:#fff;
    margin-bottom:20px;
    background:rgba(0,0,0,.6);
}
.card-list .statu-item img{
    display: block;
    width:45%;
    height:auto;
    margin:5px 2.5%;
    cursor:pointer;
}
.card-list .statu-item .statu-img{
    width:100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content:flex-start;
    align-items:flex-start;
}
.statu-item .statu-det{
    width:100%;
    font-size:14px;
    line-height:28px;
    padding-top:10px;
}
.statu-item .statu-det .t{
    font-size:16px;
}
.statu-item .statu-det .v{
    font-size:14px;
}
.statu-item .statu-det .contxt{
    font-size:14px;
    line-height:26px;
    color:#f1f1f1;
    text-indent:2em;
}
