.card-list{
    width:100%;
    overflow:hidden;
}
.card-list .statu-item{
    width:100%;
    overflow:hidden;
    padding:20px;
    color:#fff;
    margin-bottom:20px;
    background:rgba(0,0,0,.6);
}
.card-list .statu-item img{
    display: block;
    width:45%;
    height:auto;
    margin:5px 2.5%;
    cursor:pointer;
}
.card-list .statu-item .statu-img{
    width:100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content:flex-start;
    align-items:flex-start;
}
.statu-item .statu-det{
    width:100%;
    font-size:14px;
    line-height:28px;
    padding-top:10px;
}
.statu-item .statu-det .t{
    font-size:16px;
}
.statu-item .statu-det .v{
    font-size:14px;
}
.statu-item .statu-det .contxt{
    font-size:14px;
    line-height:26px;
    color:#f1f1f1;
    text-indent:2em;
}