body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size:14px;
  color:#ccc;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/*修复img默认位置和IE边框*/
img{
  vertical-align: top;
  border:none;
}

/*去除虚线*/
input:focus,select:focus,textarea:focus,button:focus{
  outline: none;
}
/*重置部分表单圆角和内阴影*/
input[type="text"],input[type="file"],input[type="password"],input[type="search"],input[type="tel"],input[type="number"],input[type="email"],input[type="url"],textarea{
  -webkit-appearance:none;
}
/*去除文本框上下箭头*/
input[type="number"]::-webkit-inner-spin-button{
  -webkit-appearance:none;
}
input{
  padding-left:5px;
}
a{
  text-decoration: none;
  /*ios禁止页面在新窗口打开*/
  -webkit-touch-callout:none;
}
body{
  background-color: #f5f5f5;
}
em,i{
  font-style: normal;
}
li{
  list-style: none;
}
html,body{
  height: 100%;
  background:#fff;
  overflow:hidden;
  /*background:#f1f1f1;*/
}
#root{
  width:100%;
  height: 100%;
  overflow:hidden;
  background:url("./static/images/timg.jpg") no-repeat;
  background-size:cover;
}


.home-content{
  position:relative;
  width:1090px;
  margin:0 auto;
  height: 100%;
  overflow:hidden;
}
.container{
  height:100%;
  margin-left:180px;
}
.rig-container{
  width: 100%;
  height: 100%;
  overflow-y:auto;
}
.item-content{
  width: 100%;
  overflow: hidden;
  padding-left:20px;
}

