.main-content{
    background:url("../../static/images/chun.jpg") no-repeat;
    background-size:cover;
    background-attachment:fixed;
    color:#fff;
    padding-top: 20px;
}
.home-staus{
    float: left;
    width:50%;
    padding:0 30px;
    overflow: hidden;
    border-right: 1px solid #ededed;
}
.home-news{
    float: left;
    width:50%;
    padding:0 30px;
    overflow: hidden;
}
.news-item{
    width: 100%;
    margin-bottom:10px;
    border-bottom:1px solid #ededed;
}
.news-item>div{
    width: 100%;
    overflow: hidden;
}
.news-item img{
    width:100%;
    height:auto;
}
.news-item .tit{
    line-height:28px;
    font-size:16px;
    font-weight:bold;
    padding:10px;
}