.head-con{
    position:relative;
    float: left;
    width:180px;
    padding:40px 20px 100px;
    height: 100%;
    background:rgba(0,0,0,.9);
}
.head-con .logo{
    width:100%;
    text-align:center;
    font-size:20px;
    font-weight: bold;
    color: lightskyblue;
    margin:60px 0 60px;
}
.head-con .nav li{
    width: 100%;
    line-height:60px;
    color:#fff;
    font-size:18px;
    text-align:left;
}
/*.head-con .nav li.now{*/
    /*color:#87CEFA;*/
/*}*/
.head-con .nav li a{
    display: inline-block;
    color:#fff;
    padding-left:36px;
}
.head-con .nav li:nth-child(1) a{
    background: url("./../../static/images/shu.png") no-repeat left center;
    -webkit-background-size: 30px;
    background-size: 30px;
}
